import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const SkipLink: React.FC = () => {
  const { t } = useTranslation();
  const [expanded, setExpanded] = useState(false);

  const skipLinks = [
    { id: 'main-content', label: t('accessibility.skipToMain', 'Pular para conteúdo principal') },
    { id: 'projetos', label: t('accessibility.skipToProjects', 'Pular para projetos') },
    { id: 'backlog', label: t('accessibility.skipToBacklog', 'Pular para backlog') },
    { id: 'contato', label: t('accessibility.skipToContact', 'Pular para contato') }
  ];

  return (
    <div 
      className={`sr-only focus-within:not-sr-only focus-within:absolute focus-within:top-0 focus-within:left-0 focus-within:right-0 focus-within:z-50 focus-within:bg-[var(--color-background)] focus-within:border-b focus-within:border-[var(--color-border)] focus-within:shadow-sm ${
        expanded ? 'focus-within:h-auto' : 'focus-within:h-10'
      }`}
    >
      <div className="flex flex-col sm:flex-row sm:justify-center sm:items-center py-2 px-4 gap-2">
        {skipLinks.map((link) => (
          <a
            key={link.id}
            href={`#${link.id}`}
            className="px-4 py-2 text-center text-[var(--color-primary)] hover:underline focus:outline-none focus:ring-2 focus:ring-[var(--color-primary)] focus:ring-offset-2 transition-all duration-200 rounded"
            tabIndex={0}
            onClick={() => {
              // Scroll to the element with offset for fixed header
              const element = document.getElementById(link.id);
              if (element) {
                const headerHeight = 64; // Adjust based on your header height
                const elementPosition = element.getBoundingClientRect().top + window.pageYOffset;
                window.scrollTo({
                  top: elementPosition - headerHeight,
                  behavior: 'smooth'
                });
              }
            }}
          >
            {link.label}
          </a>
        ))}
        
        <button
          className="sm:hidden px-4 py-1 mt-1 text-xs text-[var(--color-text-secondary)] hover:text-[var(--color-primary)] focus:outline-none"
          onClick={() => setExpanded(!expanded)}
        >
          {expanded ? t('accessibility.showLess', 'Mostrar menos') : t('accessibility.showMore', 'Mostrar mais opções')}
        </button>
      </div>
    </div>
  );
};

export default SkipLink;